
import React, {useState} from 'react';
import SAMLForm from '../ProviderForms/saml-form';
import {
    ActionList,
    ActionListItem,
    Button,
    Card,
    CardBody,
    Form,
    Text,
    TextContent, TextVariants,
    Title
} from "@patternfly/react-core";
import {SamlRequestData} from "../CommonInterfaces/idp-request-types";
import OidcStaticContent from "../ProviderForms/oidc-static-content";
import {apiCreateIdp} from "../ApiClient/idp-controller";

interface Props {
    chromeRef: any
}

const CreateOidcIdp = ({chromeRef}: Props) => {
    const [inputData, setInputData] = useState<SamlRequestData>(
        {
            identityProviderEntityId: "",
            authenticationRequestsUrl: "",
            serviceProviderEntityId: "",
            x509Certificate: "",
            providerId: ""
        }
    );

    /**
     * Triggered by the create button at the bottom of the screen, this function makes the call to the Create api
     * endpoint.
     */
    const createIDPCall = () => {
        const cleanRequest = {...inputData};
        cleanRequest.providerId = "saml-improved";
        cleanRequest.x509Certificate = inputData.x509Certificate.replace(/[\s\n\r]/gu, '');
        apiCreateIdp(chromeRef, cleanRequest)
            .then(response => response.json())
            .then((responseData) => {
                // errors that come through post authentication typically end up here
                if (responseData.error || responseData.error_description) {
                    console.error("Error Type: " + responseData.error);
                    console.error("Error Message: " + responseData.error_description);
                } else {
                    location.reload();
                }
            })
            .catch(error => {
                console.error("Error Type: " + error.message);
            })
    }

    /**
     * Called when a field gets edited. We needed a function that cloned the existing inputData, made the changes,
     * and then applied them
     * @param editEvent The event triggered when updating text within the form
     */
    const editInputFields = (editEvent:any) => {
        const existingInput = {...inputData}
        // @ts-ignore
        existingInput[editEvent.currentTarget.name] = editEvent.currentTarget.value;
        setInputData(existingInput);
    }

    return (
        <Card>
            <CardBody>
                <Form>
                    <Title headingLevel={"h1"}>SAML Creation form</Title>
                    <SAMLForm editableData={inputData} editFunction={editInputFields} />

                    {/* Statically generated content specific for configured SAML */}
                    <Title headingLevel={"h3"}>URLs required for identity provider configuration</Title>

                    <TextContent>
                        <Text component={TextVariants.p}>
                            Use these URLs to assist in completing setup within your identity provider.
                        </Text>
                    </TextContent>

                    <OidcStaticContent baseUrl={"Something"} idpAlias={"somethingElse"} />

                    <ActionList>
                        <ActionListItem>
                            <Button variant="primary" onClick={createIDPCall}>
                                Create SAML IDP
                            </Button>
                        </ActionListItem>
                    </ActionList>
                </Form>
            </CardBody>
        </Card>
    )
}
export default CreateOidcIdp;
