export abstract class IdpDisplayNames {

    private static variableConversion = {
        "identityProviderEntityId": "Identity provider Entity ID",
        "authenticationRequestsUrl": "Single sign-on authentication request URL",
        "serviceProviderEntityId": "Service provider issuer",
        "x509Certificate": "x509 certificate"
    }

    public static getPrintableString(varName :string): string {
        // @ts-ignore
        return this.variableConversion[varName];
    }
}
