import React, {useState} from 'react';
import './configured-oidc-idp.scss'
import {OidcResponseData} from "../CommonInterfaces/idp-response-types";
import {
    ActionList,
    ActionListItem, Button,
    ClipboardCopy,
    Form,
    FormGroup,
    Popover,
    Text,
    TextContent,
    TextVariants,
    Title
} from '@patternfly/react-core';
import HelpIcon from '@patternfly/react-icons/dist/esm/icons/help-icon';
import { useChrome } from "@redhat-cloud-services/frontend-components/useChrome";
import { getSSOBaseUrl } from "../../../Utils/general-utils";
import styles from "@patternfly/react-styles/css/components/Form/form";
import OIDCForm from "../ProviderForms/oidc-form";
import {DisableIdpModal} from "../Configured/disable-idp-modal";
import {IdpTestingModal} from "../IdpTesting/idp-testing-modal";
import {DeleteIdpModal} from "../Configured/delete-idp-modal";

interface Props {
    oidcData: OidcResponseData,
    recomputeAppState: () => void,
    milestoneThreeEnabled: boolean
}

const ConfiguredOidcIdp = ({ oidcData, milestoneThreeEnabled, recomputeAppState }: Props) => {
    const chrome = useChrome();
    const ssoBaseUrl = getSSOBaseUrl(chrome);
    const [inputData, setInputData] = useState<OidcResponseData>(oidcData);
    const [isTestingModalVisible, toggleTestingModalVisibility] = useState(false);
    const [isDisabledModalVisible, toggleIsDisabledModalVisible] = useState(false);
    const [isDeleteModalVisible, toggleIsDeleteModalVisible] = useState(false);

    /**
     * used to update the below field.
     */
    const updateField = () => {
        console.log("doing something with this data");
        // setInputData({});
    }

    /**
     *  determines the action button that should be displayed.
     */
    const actionButton = () => {
        if (oidcData.enabled) {
            return (
                <Button variant={"danger"} onClick={() => toggleIsDisabledModalVisible(true)}>
                    Disable
                </Button>
            )
        }
        // TODO: update + test & enable actions
        return (
            milestoneThreeEnabled && (
            <Button variant="link" onClick={() => toggleIsDeleteModalVisible(true)} isDanger>
                Delete identity provider
            </Button>
            )
        ) || (
            <>
                <Popover
                    triggerAction="hover"
                    aria-label="Hoverable popover"
                    headerContent={<div>Read-Only</div>}
                    bodyContent={<div>This page currently shows a read-only version of your configuration. Forthcoming
                        enhancements will provide the ability to edit.</div>}
                    footerContent="">
                    <ActionListItem>
                        {oidcData.enabled
                            ? <Button variant="danger" isDisabled>
                                Disable
                            </Button>
                            : <>
                                <Button variant="primary" isDisabled>
                                    Test and enable
                                </Button>
                                <Button variant="link" isDanger isDisabled>
                                    Delete identity provider
                                </Button>
                            </>
                        }
                    </ActionListItem>
                </Popover>
            </>
        )
    }

    return (
        <Form>
            <OIDCForm baseData={oidcData} editableData={inputData} editFunction={updateField} isCreation={false}/>

            <Title headingLevel={"h3"}>URLs required for identity provider configuration</Title>

            <TextContent>
                <Text component={TextVariants.p}>
                    Use these URLs to assist in completing setup within your identity provider.
                </Text>
            </TextContent>

            <FormGroup
                label="Service provider metadata URL"
                fieldId="metadata-url"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                Service provider metadata used to integrate Red Hat with your identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="metadata-url"
                    name="metadata-url"
                    isReadOnly>
                    {`${ssoBaseUrl}.well-known/openid-configuration`}
                </ClipboardCopy>
            </FormGroup>

            <FormGroup
                label="Redirect URL / Assertion Consumption Service (ACS) URL"
                fieldId="redirect-url"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The redirect URL to use when configuring your identity provider. This represents your companys SSO Portal where users authenticate to Red Hat.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon/>
                        </Button>
                    </Popover>
                }>
                <ClipboardCopy
                    type="text"
                    id="redirect-url"
                    name="redirect-url"
                    isReadOnly>
                    {`${ssoBaseUrl}broker/${oidcData.idpAlias}/endpoint`}
                </ClipboardCopy>
            </FormGroup>

            <ActionList>
                <ActionListItem>
                    {actionButton()}
                </ActionListItem>
                {isDisabledModalVisible && <DisableIdpModal idpAlias={oidcData.idpAlias} isOpen={isDisabledModalVisible}
                                                            onClose={() => toggleIsDisabledModalVisible(false)}
                                                            recomputeAppState={recomputeAppState}/>}
                {isTestingModalVisible && <IdpTestingModal idpAlias={oidcData.idpAlias} isOpen={isTestingModalVisible}
                                                           onClose={() => toggleTestingModalVisibility(false)}
                                                           recomputeAppState={recomputeAppState}/>}
                {isDeleteModalVisible && <DeleteIdpModal idpAlias={oidcData.idpAlias} isOpen={isDeleteModalVisible}
                                                         onClose={() => toggleIsDeleteModalVisible(false)}
                                                         recomputeAppState={recomputeAppState}/>}
            </ActionList>
        </Form>
    )
}

export default ConfiguredOidcIdp;
