import {OidcResponseData} from "@apps/IdpSelfService/CommonInterfaces/idp-response-types";
import {
    Button,
    FormGroup,
    FormHelperText,
    HelperText,
    HelperTextItem,
    Popover,
    TextInput
} from "@patternfly/react-core";
import styles from "@patternfly/react-styles/css/components/Form/form";
import HelpIcon from "@patternfly/react-icons/dist/esm/icons/help-icon";
import React from "react";
import {OidcHelperTextMessages} from "../Constants/oidc-helper-text-messages";

interface Props {
    baseData?: OidcResponseData,
    editableData: any,
    editFunction: any,
    isCreation: boolean
}

const OIDCForm = ({baseData, editFunction, editableData, isCreation}: Props) => {

    const canEdit = () => {
        if (isCreation) {
            return false;
        } else if (baseData && baseData.enabled) {
            return false;
        }
        return true;
    }

    return (
        <div>
            <FormGroup
                label="Issuer"
                fieldId="oidc-issuer"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The issuer is used to validate tokens generated by the identity provider and allows
                                additional security for your organization. This value is optional but recommended.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={canEdit()} onChange={editFunction} type="text" id="oidc-issuer" name="issuer" value={editableData.issuer} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.ISSUER}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Client ID"
                fieldId="oidc-client-id"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The client ID registered with the identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={canEdit()} onChange={editFunction} type="text" id="oidc-client-id" name="clientId" value={editableData.clientId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.CLIENT_ID}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Client Secret"
                fieldId="oidc-client-secret"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The client secret registered with the identity provider.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={canEdit()} onChange={editFunction} type="text" id="oidc-client-secret" name="clientSecret" value={editableData.clientSecret} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.CLIENT_SECRET}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Authorization URL"
                fieldId="oidc-authorization-url-id"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                URL that performs authentication of the end-user.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={canEdit()} onChange={editFunction} type="text" id="oidc-token-url-id" name="authorizationUrl" value={editableData.authorizationUrl} />

                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.AUTHORIZATION_URL}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Token URL"
                fieldId="authorization-url"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                URL where an authorization code can be exchanged for an access token and ID token.
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={canEdit()} onChange={editFunction} type="text" id="authorization-url" name="tokenUrl" value={editableData.tokenUrl}/>
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.TOKEN_URL}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Public Key ID"
                fieldId="publicKey"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                An identifier for mapping the keys
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={canEdit()} onChange={editFunction} type="text" id="public-key-id" name="publicKeyId" value={editableData.signingCertificateKeyId} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.PUBLIC_KEY_ID}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>

            <FormGroup
                label="Public Key"
                fieldId="publicKey"
                labelIcon={
                    <Popover
                        triggerAction="hover"
                        bodyContent={
                            <div>
                                The public key value
                            </div>
                        }>
                        <Button
                            variant="plain"
                            type="button"
                            onClick={(e) => e.preventDefault()}
                            className={`customPopover ${styles.formGroupLabelHelp}`}>
                            <HelpIcon />
                        </Button>
                    </Popover>
                }>
                <TextInput isDisabled={canEdit()} onChange={editFunction} type="text" id="public-key" name="publicKey" value={editableData.signingCertificate} />
                <FormHelperText>
                    <HelperText>
                        <HelperTextItem>{OidcHelperTextMessages.PUBLIC_KEY}</HelperTextItem>
                    </HelperText>
                </FormHelperText>
            </FormGroup>
        </div>
    )
}

export default OIDCForm;
