import './idp-not-setup.scss'
import React, {useState} from 'react';
import {
    Title,
    Text,
    Button, Icon
} from '@patternfly/react-core';
import CubesIcon from '@patternfly/react-icons/dist/esm/icons/cubes-icon';
import InfoCircleIcon from '@patternfly/react-icons/dist/esm/icons/info-circle-icon';
import PropTypes from "prop-types";
import ChooseIdpType from "./choose-idp-type";
import {useFlag} from "@unleash/proxy-client-react";
import {FlowStep} from "../Constants/flow-step";

interface Props{
    configChosen: Function,
}

const IdpNotSetup = ({configChosen}: Props) => {

    const [showProviderTypeOptions, setShowProviderTypeOptions] = useState(false);
    const isM3Enabled = useFlag('iamconsole.IDPIntegration.m3');

    const handleM3Enabled = () => {
        if (isM3Enabled) {
            setShowProviderTypeOptions(true)
        } else {
            configChosen(FlowStep.SAML_CONFIG)
        }
    }

    /**
     * this is just a conditional rendering function that determines if we display the init page or the selection page
     */
    const renderStep = () => {
        if (!showProviderTypeOptions) {
            return (
                <div className="notSetupDiv">
                    <CubesIcon className="cubesIcon"/>
                    <Title headingLevel="h2" size="xl" className="pf-v5-u-mt-lg">No Identity Providers are currently configured</Title>
                    <Text className="pf-v5-u-mt-md">Your organization has not integrated an identity provider. More information is available <Button variant="link" target="_blank" href="https://access.redhat.com/articles/7093475" component="a" isInline>here</Button>.</Text>
                    <Text><Icon status="info" isInline><InfoCircleIcon /></Icon> Configure your identity provider, test and enable immediately, or return and enable when you&apos;re ready.</Text>
                    <Button variant="primary" onClick={ () => handleM3Enabled()}>Integrate an identity provider</Button>
                </div>
            )
        }
        return (
            <ChooseIdpType configTypeChosen={configChosen}/>
        )
    }

    return renderStep();
}

IdpNotSetup.propTypes = {
    configChosen: PropTypes.func
}

export default IdpNotSetup;
