import React, {useState, useRef, useEffect} from 'react';
import { Card, CardBody, CardFooter, CardHeader, Gallery} from "@patternfly/react-core";
import {FlowStep} from '../Constants/flow-step';

interface Props {
    configTypeChosen: Function
}

const ChooseIdpType = ({configTypeChosen}: Props) => {

    return (
        <div>
            <Gallery hasGutter>

                <Card isClickable={true} onClick={() => { configTypeChosen(FlowStep.SAML_CONFIG) } }>
                    <CardHeader>
                        <h4>SAML</h4>
                    </CardHeader>
                    <CardBody>
                        <p>Awesome description about why SAML is super amazingly cool</p>
                    </CardBody>
                    <CardFooter>
                        <button>
                            Use SAML
                        </button>
                    </CardFooter>
                </Card>

                <Card onClick={() => { configTypeChosen(FlowStep.OIDC_CONFIG) } }>
                    <CardHeader>
                        <h4>OIDC</h4>
                    </CardHeader>
                    <CardBody>
                        <p>Awesome description about why OIDC is significantly easier</p>
                    </CardBody>
                    <CardFooter>
                        <button>
                            Use OIDC
                        </button>
                    </CardFooter>
                </Card>
            </Gallery>
        </div>
    )
}

ChooseIdpType.propTypes = {
    configTypeChosen: Function
}


export default ChooseIdpType;
